import axios from 'axios'

const createInstance = ({
  api_url,
  access_token_name = '@access_token',
  refresh_token_name = '@refresh_token',
  refreshTokenUrl = '/refresh-token',
  requestInterceptorConfig = {},
  unauthenticatedPaths = ['/login']
}) => {
  const axiosInstance = axios.create({ baseURL: api_url })

  axiosInstance.interceptors.request.use(config => {
    const accessToken = localStorage.getItem(access_token_name)

    return {
      ...config,
      headers: {
        ...config.headers,
        ...(requestInterceptorConfig.headers || {}),
        Authorization: `Bearer ${accessToken}`
      },
      ...requestInterceptorConfig
    }
  })

  axiosInstance.interceptors.response.use(
    response => response.data,
    async error => {
      if (error?.response?.status !== 401 || unauthenticatedPaths.includes(window.location.pathname)) {
        return Promise.reject(error)
      }

      try {
        const refreshToken = localStorage.getItem(refresh_token_name)

        const {
          data: { access_token, refresh_token }
        } = await axios.post(`${api_url}${refreshTokenUrl}`, {
          refresh_token: refreshToken
        })

        localStorage.setItem(access_token_name, access_token)
        localStorage.setItem(refresh_token_name, refresh_token)

        const { data } = await axios.request({
          ...error.response.config,
          headers: {
            ...error.response.config.headers,
            Authorization: `Bearer ${access_token}`
          }
        })

        return data
      } catch (error) {
        localStorage.clear(access_token_name)
        localStorage.clear(refresh_token_name)
        window.location.href = unauthenticatedPaths[0]
      }
    }
  )

  return axiosInstance
}

export default {
  createInstance
}
